import React, { ReactElement, useEffect, useState } from 'react'
import { VictoryBar, VictoryChart, VictoryAxis, VictoryTheme, VictoryLabel } from 'victory'
import { FiArrowLeft } from 'react-icons/fi'
import './Chart.css'

interface DataProps {
    city: string;
    donations: number;
    label?: number;
    date: string
}
interface Props {
    data: DataProps[];
    loading?: boolean;
    back: () => void
}


function CampDonationChart({ data, loading, back }: Props): ReactElement {
    const initial_data: DataProps[] = [
        { city: 'Kanpur', date: '10-07-2020', donations: 20, label: 20 },
        { city: 'Kanpur', date: '11-07-2020', donations: 20, label: 20 },
        { city: 'Kanpur', date: '12-07-2020', donations: 20, label: 20 },
        { city: 'Kanpur', date: '13-07-2020', donations: 20, label: 20 },
        { city: 'Kanpur', date: '14-07-2020', donations: 20, label: 20 },
        { city: 'Kanpur', date: '10-08-2020', donations: 20, label: 20 },
        { city: 'Kanpur', date: '20-07-2020', donations: 20, label: 20 },
    ];
    const [ chartData, setData ] = useState<DataProps[]>(initial_data)
    

    useEffect(() => {
        if(loading && data){ 
            console.log('Data from month stat',data)
            setData(data)
        }
    },[data, loading])
    return (
        <div className="container chart-container my-4">
            <FiArrowLeft size={24} onClick={back}  />
        <VictoryChart
            theme={VictoryTheme.material}
            width={800}
            height={400}
            domainPadding={100}
            animate={{
                duration: 2000,
                onLoad:{ duration: 1000 }
            }}
        >
            <VictoryLabel text="Monthly Report" x={225} y={30} textAnchor="middle"/>
            <VictoryAxis

                tickValues={chartData.map(f => f.date)}
                //tickFormat={['Jan','Feb','Mar','Apr','May','June','July','Aug','Sep','Oct','Nov','Dec']}
            />
            <VictoryAxis
                dependentAxis

                tickFormat={x => x}
            />
            <VictoryBar
                data={chartData}
                x="date"
                y="donations"
                alignment='middle'
                events={[{
                    target: "data",
                    eventHandlers: {
                        onClick: () => {
                            return [{
                                target: "data",
                                mutation: (props) => {
                                    console.log(props.data[props.index].url)
                                    return true
                                }
                            }];
                        }
                    }
                }]}
            />
        </VictoryChart>
    </div>
    )
}

export default CampDonationChart
